import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'

/*
* 获取省级信息
* */
export function provinceList() {
  return axios.post(CONFIG.API.ADDRESS_PROVINCE).then(res => res.data)
}
/*
* 获取市级信息
* */
export function cityList(opts) {
  return axios.post(CONFIG.API.ADDRESS_CITY, qs.stringify({
    provinceId :opts.provinceId,
  })).then(res => res.data)
}
/*
* 获取区信息
* */
export function countyList(opts) {
  return axios.post(CONFIG.API.ADDRESS_COUNTY, qs.stringify({
    cityId :opts.cityId,
  })).then(res => res.data)
}
/*
* 获取乡信息
* */
export function townList(opts) {
  return axios.post(CONFIG.API.ADDRESS_TOWN, qs.stringify({
    districtId :opts.districtId,
  })).then(res => res.data)
}


