<template>
  <div class="address-edit">
    <address-header style="z-index: 999;background-color: #fff" line-style="background:#fff" :title="addressTitle" :goEdit="goEdit" @goSave="goSave" @historyBack="historyBack"></address-header>
    <scroll class="address-scroll" ref="addressScroll">
      <div>
        <div class="address-wrapper" ref="addressWrapper">
          <form action="" v-model="addressForm" class="addressForm">
            <div class="addressInputWrap">
              <input type="text" v-model="addressForm.name" @focus="showInputClear(1)" @blur="hideInputClear(1)"  @click="focusInput" placeholder="收货人" />
              <div class="adressFormClose" @click="clearInput(1)" v-show="showName"><i class="iconfont">&#xe641;</i></div>
            </div>
            <div class="addressInputWrap">
              <input type="text" v-model="addressForm.mobile" @focus="showInputClear(2)" @blur="hideInputClear(2)"  @click="focusInput" placeholder="手机号码" />
             <div class="adressFormClose" @click="clearInput(2)" v-show="showMobile"><i class="iconfont">&#xe641;</i></div>
            </div>
            <div class="addressDetail" @click="toShow()"><input type="text" ref="addressInput" v-model="addressForm.address" placeholder="所在地区" /></div>
            <div class="addressInputWrap">
              <textarea v-model="addressForm.detail"  @focus="showInputClear(3)" @blur="hideInputClear(3)"  @click="focusInput" @input="listenAddress" maxlength="40"  placeholder="详细地址: 如街道、门牌号、小区、楼栋号、单元室等"></textarea>
              <div class="adressFormClose" @click="clearInput(3)" v-show="showDetail"><i class="iconfont">&#xe641;</i></div>
            </div>
            <div class="set-default" @click="changeSwitch">
              <div>设为默认地址</div>
              <div :class="['toggswitch',checked ? 'checked-toggswitch' : '' ]"></div>
            </div>
            <div class="set-default delete-address" @click="deleteAddress">
              <div>删除收货地址</div>
            </div>
          </form>
        </div>
      </div>

      <vue-pickers
        :show="show"
        :link="link"
        :columns="columns"
        :selectData="pickData"
        @cancel="close"
        @confirm="confirmFn"
        @getcitylist="getCityList"
        @getcountylist="getCountyList"
        @gettownlist="getTownList"
        ></vue-pickers>
      <div class="picker-wrap" v-show="showPickerWrap" @click="closePicker"></div>
    </scroll>
    <popup v-show="showPopup" @confirm="confirm" @cancel="cancel">
      <p style="padding: 0px 1.6rem 0.8rem; font-size: 15px; color: rgb(153, 153, 153); line-height: 1.3;">
        {{ hintInformation }}
      </p>
    </popup>
  </div>
</template>

<script>
  import AddressHeader from '../../base/address-header/address-header.vue'
  import Scroll from '../../base/scroll/scroll'
  import vuePickers from '../../base/address-popup/address-popup.vue'
  import Popup from '../../base/popup/popup'
  import * as core from '../../api/pickers'
  import * as add from '../../api/addressList'

  export default {
    name: 'address-edit',
    data() {
      return {
        goEdit: false,
        addressTitle: "编辑收货地址",
        addressForm: {
          name: "",
          mobile: "",
          address:"",
          detail:""
        },
        addressId: {
          provinceId: "",
          cityId: "",
          districtId: "",
          townId: ""
        },//个人信息
        showName: false,
        showMobile: false,
        showDetail: false,
        checked: false,//是否选中默认
        show: false, //展示四级地址信息弹窗
        showPickerWrap: false,//四级地址遮罩
        columns: 4,//列数
        link: true,//是否联动
        pickData: {
          data1: '',
          data2: '',
          data3: '',
          data4: ''
        },//四级地址信息
        showPopup: false, //展示提示弹窗
        hintInformation: "保存成功!" ,//展示提示信息
        isDelate: false,
        editData:{},
        itemAddressId: ''
      }
    },
    components: {
      AddressHeader,
      Scroll,
      vuePickers,
      Popup
    },
    created() {
      document.title = this.$route.meta.title
      this.getProvinceList()
      this.itemAddressId = this.$route.query.addressId
      this.getAddressDetail(this.$route.query);
    },
    methods: {
      focusInput(e){
        e.target.focus()
      },
      //获取四级信息
      getProvinceList: async function (){
        let that = this
        const res = await core.provinceList()
        if (res.code&&'00'===res.code){
          this.pickData.data1=res.result;

          that.getCityList({provinceId: that.pickData.data1[0].id})
        }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
        } else {
          this.$toastBox.showToastBox(res.message)
        }
      },
      getCityList : async function (opt){
        let that = this
        const res = await core.cityList(opt)
        if (res.code&&'00'===res.code){
          this.pickData.data2=res.result;

          if(that.pickData.data2[0].id){
            that.getCountyList({cityId: that.pickData.data2[0].id})
          } else {
            that.pickData.data3=[];
            that.pickData.data4=[];
          }
        }else if (res.code && '01' === res.code) {
          tool.toWeiXinLogin(window.location.href)
        } else {
          this.$toastBox.showToastBox(res.message)
        }
      },
      getCountyList: async function (opt){
        let that = this
        const res = await core.countyList(opt)
        if (res.code&&'00'===res.code){
          this.pickData.data3=res.result;

          if(that.pickData.data3.length > 1){
            that.getTownList({districtId: that.pickData.data3[0].id})
          } else {
            that.pickData.data4=[];
          }
        }else if (res.code && '01' === res.code) {
          tool.toWeiXinLogin(window.location.href)
        } else {
          this.$toastBox.showToastBox(res.message)
        }
      },
      getTownList: async function (opt){
        let that = this
        const res = await core.townList(opt)
        if (res.code&&'00'===res.code){
          this.pickData.data4=res.result;
        }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
        } else {
          this.$toastBox.showToastBox(res.message)
        }
      },
      historyBack(){
        this.$router.push({path:'/addressList',query:{pageName:"address"}})
      },
      //点击遮罩关闭picker
      closePicker(){
        this.show = false
        this.showPickerWrap = false
        this.$refs.addressInput.blur();
      },
      //picker开关
      close() {
        this.show = false
        this.showPickerWrap = false
        this.$refs.addressInput.blur();
      },
      confirmFn(val) {
        this.show = false
        this.showPickerWrap = false
        this.$refs.addressInput.blur();
        //console.log(val)
        this.addressForm.address = ""
        if(val.select1){
          this.addressForm.address += val.select1.name +" "
          this.addressId.provinceId = val.select1.id
        }
        if(val.select2){
          this.addressForm.address += val.select2.name +" "
          this.addressId.cityId = val.select2.id
        }else{
          this.addressForm.address += ""
          this.addressId.cityId = ""
        }
        if(val.select3){
          this.addressForm.address += val.select3.name +" "
          this.addressId.districtId = val.select3.id
        }else{
          this.addressForm.address += ""
          this.addressId.districtId = ""
        }
        if(val.select4){
          this.addressForm.address += val.select4.name +" "
          this.addressId.townId = val.select4.id
        }else{
          this.addressForm.address += ""
          this.addressId.townId = ""
        }
      },
      toShow() {
        this.show = true
        this.showPickerWrap = true
        this.$refs.addressInput.blur();
      },
      //是否默认地址
      changeSwitch() {
        this.checked = !this.checked
      },
      //删除收货地址
      deleteAddress(){
        this.showPopup = true
        this.hintInformation = "确定要删除该地址吗?"
        this.isDelate = true
      },
      //保存地址
      goSave(){
        if(this.addressForm.name && this.addressForm.mobile && this.addressForm.address && this.addressForm.detail){
          let telReg = /^1[345678][0-9]{9}$/;
          if (!telReg.test(this.addressForm.mobile)) {
            this.$toastBox.showToastBox("请输入正确的手机号")
            return;
          }
          //处理数据
          let allData = {
            realname: this.addressForm.name,
            mobile:   this.addressForm.mobile,
            address:  this.addressForm.detail, //详细信息
            addressId: this.itemAddressId
          }
          if(this.addressId.provinceId){
            allData.provinceId = this.addressId.provinceId
          }
          if(this.addressId.cityId){
            allData.cityId = this.addressId.cityId
          }
          if(this.addressId.districtId){
            allData.districtId = this.addressId.districtId
          }
          if(this.addressId.townId){
            allData.townId = this.addressId.townId
          }
          if(this.checked){
            allData.isDefault = "Y"
          } else {
            allData.isDefault = "N"
          }
          this.editData = allData

          //console.log(this.editData);

          this.showPopup = true
          this.isDelate = false
          this.hintInformation = "是否保存本次编辑结果?"

        } else if (!this.addressForm.name){
         this.$toastBox.showToastBox("请输入收货人姓名")
        } else if (!this.addressForm.mobile){
          this.$toastBox.showToastBox("请输入收货人手机号码")
        } else if (!this.addressForm.address){
          this.$toastBox.showToastBox("请选择所在地区")
        } else if (!this.addressForm.detail){
          this.$toastBox.showToastBox("请输入详细地址")
        }

      },
      //提示框开关
      confirm() {
        if(this.isDelate){
          add.itemAddressDelete({addressId: this.itemAddressId}).then(res=>{
            //console.log(res)
            if (res.code&&'00'===res.code){
              this.showPopup = false
              this.$toastBox.showToastBox("删除成功")
              this.$router.push({path:'/addressList',query:{pageName:"address"}})
            }else if (res.code && '01' === res.code) {
              tool.toWeiXinLogin(window.location.href)
            } else {
              this.showPopup = false
              this.$toastBox.showToastBox(res.message)
            }
          })
        } else {
          //console.log(this.editData)
          add.itemAddressEdit(this.editData).then(res=>{
            //console.log(res)
            if (res.code&&'00'===res.code){
              this.showPopup = false
              this.$toastBox.showToastBox("保存成功")
              clearTimeout(timer)
              let timer = setTimeout(()=>{
                this.$router.push({path:'/addressList',query:{pageName:"address"}})
              },1000)
            } else if (res.code && '01' === res.code) {
                tool.toWeiXinLogin(window.location.href)
            } else {
              this.showPopup = false
              this.$toastBox.showToastBox(res.message)
            }
          })
        }
      },
      cancel() {
        this.showPopup = false
      },
      // 获取编辑内容
      getAddressDetail(opt){
        add.itemAddressQuery(opt).then(res=>{
          //console.log(res)
          if (res.code&&'00'===res.code){
            this.addressForm.name = res.result.realname
            this.addressForm.mobile = res.result.mobile
            this.addressForm.address = ""
            if(res.result.province){
              this.addressForm.address += res.result.province + " "
              this.addressId.provinceId = res.result.provinceId
            }
            if(res.result.city){
              this.addressForm.address += res.result.city + " "
              this.addressId.cityId = res.result.cityId
            }
            if(res.result.district){
              this.addressForm.address += res.result.district + " "
              this.addressId.districtId = res.result.districtId
            }
            if(res.result.town){
              this.addressForm.address += res.result.town
              this.addressId.townId = res.result.townId
            }
            this.addressForm.detail = res.result.address

            if(res.result.isDefault && res.result.isDefault == "Y"){
              this.checked = true
            } else {
              this.checked = false
            }
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        })
      },
      //清楚input数据
      clearInput(val){
        if(val === 1){
          this.addressForm.name = ""
          this.showName = false
        } else if(val === 2) {
          this.addressForm.mobile = ""
          this.showMobile = false
        } else if(val === 3) {
          this.addressForm.detail = ""
          this.showDetail = false
        }
      },
      //展示清除按钮
      showInputClear(val) {
        if(val === 1){
          this.showName = true
        } else if(val === 2) {
          this.showMobile = true
        } else if(val === 3) {
          this.showDetail = true
        }
      },
      //隐藏清除按钮
      hideInputClear(val){
        if(val === 1){
          this.showName = false
        } else if(val === 2) {
          this.showMobile = false
        } else if(val === 3) {
          this.showDetail = false
        }
      },
      listenAddress(){
        var txtVal = this.addressForm.detail.length;
        if(txtVal == 40){
          this.$toastBox.showToastBox("详细地址最多40字")
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .address-edit
    background-color rgb(245,245,245)
    min-height 100vh
    overflow hidden

  .address-scroll
    //height cacl(100% - 2.75rem)
    margin 0.625rem 0

  .addressForm
    //background-color rgb(255,255,255)
    //margin 0.625rem 0
    .addressInputWrap
      position relative
      .adressFormClose
        position absolute
        right 1.875rem
        z-index 100
        top 50%
        transform translateY(-50%)
        text-align center
        color #fff
        display flex
        align-items center
        text-align center
        i
          display inline-block
          width 100%
          height 100%
          background rgb(203,203,203)
          border-radius 50%


    input
      -webkit-user-select : text
      width 100%
      margin-top 1px
      height 3.125rem
      font-size 1rem
      outline none
      border none
      padding 0 1rem
      box-sizing border-box
      font-family 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif, 'Droid Sans Fallback'

    textarea
      -webkit-user-select : text
      width 100%
      margin-top 1px
      height 3.125rem
      font-size 1rem
      outline none
      border none
      padding 0.8125rem 1rem
      height 5.8125rem
      box-sizing border-box
      line-height 1.375rem
      font-family 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif, 'Droid Sans Fallback'

    .addressDetail
      width 100%
      font-size 1rem
      position relative
      &::after
        content: '';
        position: absolute;
        width: 0.625rem;
        height: 0.625rem;
        border: 2px solid rgb(153,153,153);
        border-width: 2px 0 0 2px;
        -webkit-transform: rotate(135deg);
        transform: rotate(135deg);
        top: 1.2rem;
        right: 1rem;

    .set-default
      margin 0.625rem 0
      height 3.125rem
      background-color #fff
      line-height 3.125rem
      display flex
      justify-content space-between
      align-items center
      padding 0 1rem

      .toggswitch
        width 3.1875rem
        height 1.9375rem
        border 0.1rem solid rgb(229,229,229)
        background-color #fff
        border-radius 1.25rem
        position relative

        &::after
          content: ''
          position absolute
          left 0rem
          top 0rem
          display block
          width 1.75rem
          height 1.75rem
          border-radius 50%
          //-moz-box-shadow:2px 2px 2px #333333; -webkit-box-shadow:2px 2px 2px #333333; box-shadow:2px 2px 2px #333333;
          border 0.1rem solid rgb(229,229,229)
          transition all .3s
          background-color #fff

      .checked-toggswitch
        border 0.1rem solid rgb(9,187,7)
        background-color rgb(9,187,7)
        &::after
          left 1.2375rem
          border 0.1rem solid rgb(255,255,255)

    .delete-address
      color: rgb(255,73,73)

  .picker-wrap
    position fixed
    width 100%
    height 100%
    overflow hidden
    top 2.75rem
    background rgba(0,0,0,0.3)
    z-index 500

</style>



